.contact-us{
    padding: 60px;
    background-color: aqua;
}
.contact-us-container{

    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.contact-us-form{
    gap:2rem;
    display: flex;
    flex-direction: column;
}
.contact-us-form-input{
    font-size: 23px;
    height: 40px;
    margin: 0px auto 0px auto;
    width: 350px;
    border-radius: 10px;
}
.contact-us-form-text{
    font-size: 23px;
    height: 80px;
    margin: 0px auto 0px auto;
    width: 350px;
    border: 10px 1px solid black;
}
.contact-us-form-submit{
    margin: 0px auto 0px auto;
    padding: 5px;
    font-size: 25px;
    width: 170px;
    border: 1px solid black;
    border-radius: 20px;
    background-color: green;
    color: white;
}

.contact-us-image{
    width: 500px;

}
.contact-us-container-main{
    padding: 20px;
    height: 600px;
    background-color: white;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
}


@media (max-width:491px){
    .contact-us{
        padding: 40px;
        background-color: aqua;
    }
}
@media (max-width:427px){
    .contact-us{
        padding: 25px;
        background-color: aqua;
    }
}
@media (max-width:1114px){
    .contact-us-image{
        width: 400px;
    
    }
    .contact-us-container{

        margin: auto;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }
}
@media (max-width:963px){
    .contact-us-container{
        flex-direction: column;
    }
    .contact-us-container-main{
        padding: 20px;
        height:900px;
        background-color: white;
        border-radius: 20px;
        display: flex;
        flex-direction: column;
    }
}
@media (max-width:630px){
    .contact-us{
        padding: 20px;
        background-color: aqua;
    }
}
@media (max-width:520px){
    .contact-us-image{
        width: 270px;
    
    }
    .contact-us-container-main{
        padding: 10px;
        height: 750px;
    }
    .contact-us-form-input{
        width: 300px;
    }
    .contact-us-form-text{
        width: 300px;
    }
}