.header-container{
    background-color: black;
    display: flex;
    flex-direction: row;
}
.header-heading{ 
    color: white;
    margin: 10px;
    margin-left: 20px;
    text-decoration: none;
    font-size: 25px;
}

.header-feature{
    margin: 10px;
    margin-left: 300px;
    
}
.nav-link{
    color:white;
}



@media (max-width:1008px){
    .header-feature{
        margin: 10px;
    }
    .header-container{
        background-color: black;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
}
@media (max-width:705px){
    .header-container{
        background-color: black;
        display: flex;
        flex-direction: column;
    }
    .header-feature{
        margin:0px auto 5px auto ;
    }
}